export default {
	data() {
		return {
			idx:'-1',
			list:[],
			listItem:''
		};
	},
	onShow(){
		console.log(111)
		this.initData();
	},
	onReady() {
		this.initData();
	},
	methods:{
		// 选择风格
		select(index,item,img){
			this.idx = index;
			this.listItem = item;
			this.$emit('designManner',this.idx,this.listItem,img);
		},
		// 初始数据
		initData(){
			this.$emit('designManner',this.idx);
			let categorys;
			if (uni.getStorageSync("catalogues")) {
				categorys = uni.getStorageSync("catalogues").categorys;
			} else if (this.$store.state.catalogues) {
				categorys = this.$store.state.catalogues.categorys;
			}
			if (categorys && categorys.length > 0) {
				let list= categorys.filter((item)=>{
					return item.type == 2;
				})
				this.list = list;
				this.list.map(item=>{
					item.backgImg = item.backgImg + '?imageView2/1/w/750/h/498/interlace/1';
				})
			}
			
		}
	}
}
